.navbar-brand {
    display: flex;
    text-align: center;
    align-items: center;
}
.navbar-brand h1 img{
    color: #d5aa6d;
    width: 2rem;
}
.navbar-brand h1 span {
    color: black;
}
.navbar-brand span p{
    font-size: 1rem;
    color: grey;
    text-align: left;
    padding-left: 2vw;
    margin-left: 2vw;
    border-left: 1px solid grey;
}
.myUL{
    justify-content: end;
}

.myNavbar{
    padding: 0 5vw;
}




@media (max-width:990px) {
    .navbar-brand span p{
        display: none;
    }
    .myUL{
        background-color: rgb(34, 29, 29);
    }
    .myA {
        color: white;
    }
    .navbar-nav .nav-link.active, .navbar-nav .show>.nav-link {
        color: white !important
    }
    .myNavbar{
        padding: 0rem;
    }
}