.carousel-item{
    height: 90vh;
}   
.ci1, .ci2, .ci3{
    height: 90vh;
    background-position: center;
    background-size: cover;
    background: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5));
}


.navbtn button {
    padding: 1vw 2vw;
    background: none;
    border: 1px solid white;
    color: white;
    font-size: 1vw;
    font-weight: 700;
    letter-spacing: -1px;
    cursor: pointer;

}
.navbtn button:hover{
    background-color: rgba(171, 171, 48, 0.918);
    transition: 0.4s;
    border: 1px solid transparent;
}
.text h2{
    font-size: 3vw;
}
.bg h1 {
    font-size: 3.2vw;
    font-weight: 900;
    margin: 0;
}
.bg p {
    font-size: 1.2vw;
}
.myCarousel .CI .out{
    display: flex;
    text-align: center;
    color: white;
    justify-content: center;
    align-items: center;
    margin-top: 17rem;
}
.ci1, .ci2, .ci3{
    background-position: center;
    background-size: cover;
}