.line{
    display: flex;
    width: fit-content;
    margin: auto;
    
}
.numbers .myCard{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-left: 1px solid grey;
    border-bottom: 1px solid grey;
    width: fit-content;
    width: 20vw;
    padding: 1vw;
}
.numbers .myCard .text h1 {
    font-size: 5vw;
    margin-top: -3vw;
}
.numbers .myCard .text h2{
    font-size: 3vw;
    margin-top: -2vw;
}
.numbers .myCard .text p{
    font-size: 1vw;
}
.numbers .myCard .myPng img {
    width: 4vw;
}



.welcome{
    display: flex;
    width: 40vw;
    margin-left: 9vw;
    margin-top: 4vw;
}
.yLine{
    width: 12vw;
    height: 2px;
    margin-top: 4vw;
    margin-right: -2vw;
    background-color: rgb(190, 160, 24);
}
.wText{
    padding: 2vw 4vw;
}
.wText h3 {
    font-size: 1.5vw;
}
.wText h1 {
    font-size: 2vw;
    font-weight: 700;
}
.wText p {
    font-size: 1.2vw;
    color: grey;
}


.storyLine{
    display: flex;
    justify-content: center;
}
.story{
    display: flex;
    flex-direction: column;
    width: 25vw;
    padding: 0 2vw;
}
.story .fline{
    display: flex;
    justify-content: space-between;
}
.story .fline h2 {
    font-size: 2vw;
}
.story .fline h1 {
    font-size: 4vw;
    color: grey;
}
.story h3 {
    font-size: 1.2vw;
    color: rgb(156, 156, 8);
}
.story .font{
    margin: 1.5vw 0;
}
.story .font p{
    font-size: 1vw;
    color: grey;
}
.btn{
    margin: 5vw auto;
    width: 100%;
}
.btn button {
    padding: 1vw 2vw;
    background: none;
    border: 1px solid grey;
    color: black;
    font-size: 1vw;
    font-weight: 700;
    letter-spacing: -1px;
    cursor: pointer;
}
.btn button:hover{
    background-color: rgba(171, 171, 48, 0.918);
    transition: 0.4s;
    border: 1px solid transparent;
    color: white;
}

