.main{
    display: flex;
}
.main .fchild {
    width: 45%;
}
.main .fchild img {
    width: 100%;
}
.fchild .horse {
    position: absolute;
    margin-top: -35vw;
    margin-left: 15vw;
    padding: 6vw;
    border: 2px solid white;
}
.fchild .horse img{
    width: 10vw;
}
.secchild{
    width: 55vw;
    padding: 2vw 7vw;
}
.secchild .head h1 {
    font-size: 2.5vw;
}
.secchild .head p {
    font-size: 1.3vw;
    color: grey;
}
.secchild .firline .advice{
    display: flex;
}
.secchild .firline {
    display: flex;
    margin-top: 2vw;
}
.secchild .firline img{
    width: 2vw;
    margin-right: 3vw;
}
.secchild .firline .advice h1 {
    font-size: 1.4vw;
    font-weight: 700;
}
.secchild .firline .advice p {
    font-size: 0.9vw;
    color: grey;
}
.secchild .firline .advice .text {
    width: 60%;
}